import $ from "jquery";

$(document).ready(function() {

    $('body .accordion').on('click', '.accordion__item', function(){
        if ($(this).hasClass('accordion__item--active')) {
            $(this).removeClass('accordion__item--active');
        } else {
            $(this).parents('.accordion').find('.accordion__item').removeClass('accordion__item--active');
            $(this).addClass('accordion__item--active');
        }

    });
});

