ymaps.ready(init);

function init(){

    var myMap = new ymaps.Map("map", {
        center: [59.95635376519322,30.31010306085205],
        zoom: 17,
        controls: [],
    });

    myMap.geoObjects.add(new ymaps.Placemark([59.95635376519322,30.31010306085205], {
        iconCaption: ' -, .49',
    }, {
        preset: 'islands#redDotIcon',

    }));
}
