import $ from "jquery";
import ajax from "jquery";
import inputmask from "../../../js/import/jquery.inputmask.min";


$(document).ready(function() {

    $('#user-tel').inputmask('+7 (999) 999-99-99');
    $('#user-email').inputmask({
        mask: "*{1,20}[.*{1,20}][.*{1,20}][.*{1,20}]@*{1,20}[.*{2,6}][.*{1,2}]",
        greedy: false,
        onBeforePaste: function (pastedValue, opts) {
            pastedValue = pastedValue.toLowerCase();
            return pastedValue.replace("mailto:", "");
        },
        definitions: {
            '*': {
                validator: "[0-9A-Za-z!#$%&'*+/=?^_`{|}~\-]",
                casing: "lower"
            }
        }
    });

    $('body').on('click', '.modal__open-btn', function () {
        $('body').find('.modal-request').fadeIn('slow');
        $('body').addClass('overflow-hidden');
    });

    $('body').on('click', '.modal__close-btn', function () {
        $('body').find('.modal').fadeOut('slow');
        $('body').removeClass('overflow-hidden');
    });

    $(document).click(function(event) {
    	if ($('body .modal').css('display') == 'block' && !$(event.target).closest('.modal__body,.modal__open-btn').length) {
            $('body').find('.modal').fadeOut('slow');
            if ($('body').hasClass('overflow-hidden')) {
				$('body').removeClass('overflow-hidden');
			}
        }
    });

	$('.modal__form').submit(function(e){

		$.ajax({
			url: "index.html",
			type: "GET",
			async: false,
			success: function(response) {
				$('body').find('.modal').fadeOut('slow');
				$('.modal__form').trigger("reset");
				$('body').find('.modal-thanks').fadeIn('slow');
			},
		});
		e.preventDefault();
	});



});